body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.wrapper{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  border-radius: 20px;
  width: 100vw;
  height: 100%;
}
.calculator__wrapper{
  width: 35vw;
  background-color: #E7E7E7;
  margin-top: 50px;
  margin-bottom: 50px;
  border-radius: 20px;
  box-shadow: 0 0 10px black;
}
.small{
  text-align: center;
}

/*MEDIA QUERY*/
@media screen and (max-width: 833px){
  .calculator__wrapper{
    margin-top: 50px;
    width: 90vw;
  }
}
@media screen and (max-width: 412px){
  h4{
    font-size: 14px;
  }
  p{
    font-size: 12px;
  }
  button{
    font-size: 12px;
  }

}

@media screen and (max-width: 320px){
  h4{
    font-size: 12px;
  }
  p{
    font-size: 11px;
  }
  button{
    font-size: 11px;
  }

}

h4{
  margin: 0;
  padding: 0;
}
.result{
  width: 100%;
  text-align: center;
}
.norm{
  width: 100%;
}

.DailyQuantity{
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
}
.DailyQuantity h4{
  line-height: 50px;
}
.DailyQuantity__wrapper{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
}

.input{
  border-radius: 20px;
  text-align: center;
}

div{
  color: black;
}
p{
  margin: 0px
}
input{
  border-radius: 20px;
  text-align: center;
  width: 80%;
}

.main{
  width: 100%;
  background-color: #E7E7E7;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
.aim{
  width: 100%;
  text-align: center;
}
.button__wrapper{
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin-top: 20px;
  border-bottom-left-radius: 20px;
}
.Input__data{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

button{
  height: 30px;
  border-radius: 20px;
  margin-bottom: 20px;
  box-shadow: 3px 3px 5px 0 rgba(36,36,36,.2);
}

.Inputs{
  margin-bottom: 20px;
}

